import api  from '../../../utils/api.js';
import util from '../../../utils/util.js';
export default {
	data() {
		return {
			searchValue:'',
			params: {
				task_type: 1,
				type: -1,
			},
		    page: 1,
		    status: 0,
		    multiIndex: [7, 0, 8],
		    orderList:[],
		    isFullLoad:false,
		    address:'',
		    name:'',
			navTexts:['全部','待接单','派送中','待收货','待评价','已完成'],
			isLogistics:false,
			express_name:'',
			express_phone:'',
			express_sn:'',
			result_img:'',
			isiP:false,
			scrolLeft:1
		};
	},

	onLoad(options) {
		
		uni.setNavigationBarTitle({
			title: "配送订单"
		})
		
		 switch(uni.getSystemInfoSync().platform){
			case 'android':
			   console.log('运行Android上')
			   break;
			case 'ios':
			   console.log('运行iOS上')
			   this.$data.isiP = true
			   break;
			default:
			   console.log('运行在开发者工具上')
			   break;
		}
		
	},
	onShow() {
		console.log('onShow')
		// 获取分类数据
		this.$data.status = wx.getStorageSync('status') || 0;
		this.$data.orderList = [] 
		
		  // 获取数据
		this.getMyOrders();
	},
	mounted(){
		console.log('mounted')
		// 获取分类数据
		this.$data.status = wx.getStorageSync('status') || 0;
		this.$data.orderList = [] 
		
		  // 获取数据
		this.getMyOrders();
	},
	methods: {
		
		// 搜索
		doSearch(){
			if(this.$data.searchValue == ''){
				util.errorTips('搜索关键字不能为空');
				return false;
			}
			api.apiStaffShipSearch({
				data:{
					keyword:this.$data.searchValue
				}
			}).then((res)=>{
				if(res.code == 200 || res.code == 0){
					if(res.data.length>0){
						this.$data.status = 0;
						this.$data.scrolLeft = 0;
						this.$data.orderList = [];
						this.$data.orderList = res.data;
					}else{
						util.errorTips('搜索不到相关数据')
					}
				}else{
					util.errorTips(res.msg)
				}
			}).catch((e)=>{util.errorTips(e.msg || e.message)})
		},
		
		logisticsSubmit(){
			if(this.$data.express_name == ""){
				util.errorTips('请填写物流公司');
				return false;
			}
			if(this.$data.express_phone == ""){
				util.errorTips('请填写物流电话');
				return false;
			}
			if(!util.vailPhone(this.$data.express_phone)){
				util.errorTips('请填写正确的电话');
				return false;
			}
			
			if(this.$data.express_sn == ""){
				util.errorTips('请填写物流单号');
				return false;
			}
			api.staffShipExpress({
				method:'POST',
				data:{
					id:this.$data.id,
					express_name:this.$data.express_name,
					express_phone:this.$data.express_phone,
					express_sn:this.$data.express_sn,
					express_img:this.$data.result_img
				}
			}).then((res)=>{
				if(res.code == 0 || res.code == 200){
					this.$data.isLogistics = false;
					this.$data.orderList = [] 
					  // 获取数据
					this.getMyOrders();
				}else{
					util.errorTips(res.msg);
				}
			}).catch((res)=>{
				util.errorTips(res.msg || res.meessage);
			})
		},
		hideLogistics(){
			this.$data.isLogistics = false;
		},
		// 送达
		serviceOrder(item){
			let _this = this;
			let id = item.id;
			let shipping_type = item.shipping_type;
			this.$data.result_img = item.result_img[0];
			this.$data.id = item.id;
			uni.showModal({
				title: '提示',
				content: '确认送达?',
				success: function (res) {
					if (res.confirm) {
						console.log('用户点击确定');
						if(shipping_type != 2 ){ // 送货上门
							api.staffShipConfirm({
								method:'POST',
								data:{
									id
								}
							}).then((res)=>{
								if(res.code == 0 || res.code == 200){
									_this.$data.orderList = [] 
									  // 获取数据
									_this.getMyOrders();
								}
							})
						}else{  // 物流
							_this.$data.isLogistics = true;
						}
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
			
		},
		// 联系客户
		contact(item){
			let data={
				id:item.id,
				type:2
			}
			api.apiPhoneStaff({
				method:'POST',
				data
			}).then((res)=>{
				if(res.code == 200 || res.code == 0){
					uni.makePhoneCall({
						phoneNumber: res.data
					})
				}else{
					util.errorTips(res.msg);
				}
			}).catch((res)=>{
				util.errorTips(res.msg)
			})
		},
		
		// 取聊天室
		goChat(item){
			
			uni.navigateTo({
				url:'/pages/chat/chat?fromUserId='+uni.getStorageSync('userInfo').id+'&toUserId=' + item.user_id + '&name=客户'
			})
		},
		// 搜索
		// doSearch(){
		// 	
		// 	this.$data.scrolLeft = 0;
		// 	if(this.$data.searchValue == ''){
		// 		util.errorTips('搜索关键字不能为空');
		// 		return false;
		// 	}
		// 	this.$data.page = 1;
		// 	api.staffShipSearch({
		// 		data:{
		// 			page:this.$data.page,
		// 			keyword:this.$data.searchValue
		// 		}
		// 	}).then((res)=>{
		// 		if(res.code == 200 || res.code == 0){
		// 			this.$data.orderList = [];
		// 			this.$data.status = 0;
		// 			this.$data.orderList = this.$data.orderList.concat(res.data);
		// 		}
		// 	})
		// },
		
		// 确认接单
		receiptOrder(id){
			let _this = this;
			uni.showModal({
				title: '提示',
				content: '确定接单?',
				success: function (res) {
					if (res.confirm) {
						console.log('用户点击确定');
						api.staffShipGet({
							method:'POST',
							data:{
								id
							}
						}).then((res)=>{
							if(res.code == 200 || res.code == 0){
								//_this.$data.status = 2; // 状态2 已接单
								_this.$data.orderList = [] ;
								
								  // 获取数据
								_this.getMyOrders();
								util.successTips("接单成功");
							}
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
			
		},
		// 获取数据
		  getMyOrders(){
			  let s  = 0;
			  if(this.$data.status >= 1){
				  s =  this.$data.status+1;
			  }
			   api.myOrderShipList({
				 data: {
					 page: this.$data.page,
					 status:s
				 }
			   }).then((res) => {
				   if(res.code == 200 || res.code == 0){
					   if(res.data.length <10) this.$data.isFullLoad = true;
					   this.$data.orderList = this.$data.orderList.concat(res.data);
				   }else{
					   // if(res.msg == "您不是配送员"){
						  //  uni.navigateTo({
						  //  	url:'/pages/login/login?from='+ uni.getStorageSync('userType')
						  //  })
					   // }
				   }
			   }).catch((res)=>{
				   util.errorTips(res.msg || res.message)
			   })
			
		  },
		  
		  // 大类选择
			getOrderData(e) {
				this.$data.nav = e.currentTarget.dataset.nav;
				uni.setStorageSync('nav',this.$data.nav);
				uni.setStorageSync('status', 0);
				this.$data.isFullLoad = false;
				this.$data.page = 1;
				this.$data.status = 1;
				this.$data.orderList = [];
				
				this.getMyOrders()
			},
			// 获取下栏目数据
			getOrderTypeData(e) {
				this.$data.status = e.currentTarget.dataset.type;
				uni.setStorageSync('status', this.$data.status);
				this.$data.page = 1;
				this.$data.isFullLoad = false;
				this.$data.orderList = [];
				this.getMyOrders()
			},
			
			// 查看详情
			seeDetail(e) {
			  let index = e.currentTarget.dataset.index;
			  let id = e.currentTarget.dataset.id;
			  uni.navigateTo({
				url: "../index/fetchPage/fetchOrderDetail/fetchOrderDetail?id="+id
			  })
				
			},

			// 预览图片

			preview(e) {
				let index = e.currentTarget.dataset.index;
				let idx = e.currentTarget.dataset.idx;
			    let imgs = this.$data.orderList[index].desc_img;
				uni.previewImage({
					current: imgs[idx], // 当前显示图片的http链接
					urls: imgs // 需要预览的图片http链接列表
				})
			},
	},
	onPullDownRefresh: function () {
		this.$data.page++;
		this.getMyOrders();
		uni.stopPullDownRefresh()
	},
	onReachBottom: function () {
	  if (!this.$data.isFullLoad){
		this.$data.page++;
		this.getMyOrders();
	  }
	},
}
